import Scroll from "@/components/scroll";
export default {
  data() {
    return {};
  },
  components: {
    "v-scroll": Scroll
  },
  mounted() {},
  methods: {
    onRefresh(done) {
      done()
    },
    onInfinite(done) {
      // alert('9')
      done()
    }
  }
};



